body {
    width: 40%;
    margin-left: 10%;
    border-right: 2px solid gray;
    border-left: 2px solid gray;
}

@media screen and (max-width: 768px) {
    body {
        width: 100%;
        margin-left: 0;
        border-right: none;
        border-left: none;
    }
}

.signIn {
    margin-top: 100px;
    display: grid;
    justify-content: center;
    align-items: center;
}
.signN {
    background-color: #395dff;
    display: grid;
    justify-content: center;
    height: 25vh;
    border-radius: 15px;
    width: 300px;
}

.myDiv {
  position: fixed;
  background-color: white;
  box-shadow: 0 0 10px rgb(164, 164, 164);
  display: flex;
  justify-content: right;
  width: 100%;
  height: 30px;
  top: 0;
}

.signOut {
  margin: 5px;
  background-color: white;
  border: 1px solid gray;
  color: gray;
}
img {
    border-radius: 50%;
    height: 45px;
    margin-top: -20px;
  }
  
  p {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    font-size: 25px;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    overflow-wrap: break-word;
  }
  
  .msg {
    display: flex;
    padding: 10px 10px 0 20px;
    margin: 10px;
    border-radius: 3000px;
    box-shadow: 0 0 10px rgb(164, 164, 164);
    align-items: center;
    max-width: 25vw;
  }

  @media screen and (max-width: 768px) {
      .msg {
        display: flex;
        padding: 10px 10px 0 20px;
        margin: 10px;
        border-radius: 3000px;
        box-shadow: 0 0 10px rgb(164, 164, 164);
        align-items: center;
        max-width: 45vw;
      }
  }
  
  .sent {
    background-color: #395dff;
    color: white;
    border-top-right-radius: 1000px;
    flex-direction: row-reverse;
    padding: 20px 20px 0 10px;
    text-align: left;
    float: right;
  }
  
  .recieved {
    border: 1px solid lightgray;
    background-color: #FAFAFA;
    border-top-left-radius: 1000px;
    float: left;
    
  }
  
  .sendMsg {
    position: fixed;
    display: flex;
    width: 100%;
    bottom: 0;
    z-index: 1;
    border-top: 1px solid lightgray;
    margin-left: 0;
    padding: 10px;
    padding-bottom: 30px;
    background-color: #fafafa;
  }

  @media screen and (max-width: 768px) {
    .sendMsg {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 0;
    }
  }

  .myFile {
    display: flex;
    align-items: center;
    position: absolute;
    background-color: whitesmoke;
    width: 69%;
    height: 100px;
    right: 92px;
    
    margin-bottom: 50px;
  }
  /* .myFil {
    display: none;
  }
  .myFile input {
    color: black;
  } */

  .myInput {
      width: 20%;
      height: 30px;
      /* margin-left: 70px; */
      border-radius: 10px;
      border: 2px solid blue;
  }
  .myBtn {
    width: 5%;
    border-radius: 10px;
    border: 2px solid blue;
 }
  @media screen and (max-width: 768px) {
    .myInput {
        width: 70%;
        word-break: break-all;
        max-height: 60px;
        min-height: 20px;
        margin-left: 0;
        resize: none;
    }
    .myBtn {
        width: 50px;
        height: 30px;
        border-radius: 10px;
        background-color: #395dff;
        color: white;
    }
  }
  
  
  .msgs {
    margin: 110px 0;
    display: flex;
    flex-direction: column;
  }
  
  
  @media (max-width: 775px) {
    p {
      font-size: 20px;
    }
    .sent {
      padding: 10px 10px 0 10px;
    }
    .received {
      padding: 10px 10px 0 10px;
    }
    img {
      height: 35px;
      margin-top: -10px;
    }
  }
  
  @media (max-width: 500px) {
    p {
      font-size: 15px;
    }
    .sent {
      padding: 7px 7px 0 7px;
    }
    .received {
      padding: 7px 7px 0 7px;
    }
    img {
      height: 30px;
      margin-top: -7px;
    }
  }
  